import React, { useRef, useState } from 'react';
import './App.css';
import './App.scss';
import googlelogo from './images/btn_google_signin_dark_pressed_web@2x.png'


import firebase from 'firebase/compat/app';
import 'firebase/compat/firestore';
import 'firebase/compat/auth';
import 'firebase/compat/analytics';

import { useAuthState } from 'react-firebase-hooks/auth';
import { useCollectionData } from 'react-firebase-hooks/firestore';

firebase.initializeApp({
  apiKey: "AIzaSyDRPjTVFTrm2qJp0_Pp-ILXJqlj66tS3jk",
  authDomain: "natecord-d1d2c.firebaseapp.com",
  projectId: "natecord-d1d2c",
  storageBucket: "natecord-d1d2c.appspot.com",
  messagingSenderId: "446540198769",
  appId: "1:446540198769:web:5c845bc62553ac674afcd0",
  measurementId: "G-T4775K0V9R"
})

const auth = firebase.auth();
const firestore = firebase.firestore();
const analytics = firebase.analytics();


function App() {

  const [user] = useAuthState(auth);

  return (
    <div className="App">
     

      <section>
        {user ? <ChatRoom /> : <SignIn />}
        
      </section>

    </div>
  );
}

function SignIn() {

  const signInWithGoogle = () => {
    const provider = new firebase.auth.GoogleAuthProvider();
    auth.signInWithPopup(provider);
  }

  return (
    <>
      <div className="sing-in_container">
      <img src={googlelogo} onClick={signInWithGoogle} className="signin_btn"/>
      <p>Do not violate the community guidelines or you will be banned for life!</p>
      </div>
    </>
  )

}

function SignOut() {
  return auth.currentUser && (
    <button className="sign-out" onClick={() => auth.signOut()} className="btn">Sign Out</button>
  )
}


function ChatRoom() {
  const dummy = useRef();
  const messagesRef = firestore.collection('messages');
  const query = messagesRef.orderBy('createdAt').limit(25);

  const [messages] = useCollectionData(query, { idField: 'id' });

  const [formValue, setFormValue] = useState('');


  const sendMessage = async (e) => {
    e.preventDefault();

    const { uid, photoURL, displayName } = auth.currentUser;

    await messagesRef.add({
      text: formValue,
      createdAt: firebase.firestore.FieldValue.serverTimestamp(),
      uid,
      photoURL,
      displayName
    })

    setFormValue('');
    dummy.current.scrollIntoView({ behavior: 'smooth' });
  }

  return (<>
  <div className="sidebar">
  <header>
        <h1>Welcome to Natecord</h1>
        <SignOut />
      </header>
  </div>
    <main className="message-container">

      {messages && messages.map(msg => <ChatMessage key={msg.id} message={msg} />)}

      <span ref={dummy}></span>

    </main>
    <section className="form-container">
    <form onSubmit={sendMessage}>

      <input className="form__field" value={formValue} onChange={(e) => setFormValue(e.target.value)} placeholder="say something nice" />

      <button type="submit" disabled={!formValue} className="form__button">üïäÔ∏è</button>

    </form>
    </section>
  </>)
}


function ChatMessage(props) {
  const { text, uid, photoURL, displayName } = props.message;

  const messageClass = uid === auth.currentUser.uid ? 'sent' : 'received';

  return (<>
    <div className={`message ${messageClass}`}>
      <img src={photoURL || 'https://api.adorable.io/avatars/23/abott@adorable.png'} />
      <div className="content">
      <h4 className="name">{displayName}</h4>
      <p>{text}</p>
      </div>
    </div>
  </>)
}


export default App;